<template>
	<view>
		<view class="status_bar"><!-- 这里是状态栏 --></view>

		<!-- 自定义顶部导航栏 -->
		<pageHeader />
		<!-- list -->
		<view class="news">
			<news-item @itemClick="goDetail" :list="newsList"></news-item>
		</view>
	</view>
</template>

<script>
	import pageHeader from './pageHeader.vue'
	import newsItem from '../../../components/news-item/news-item.vue'
	export default{
		data() {
			return {
				pageIndex: 1,
				newsList: []
			}
		},
		methods: {
			async getNews () {
				const res = await this.$myRuquest({
					url: '/app/getNewsList?pageIndex='+this.pageIndex +'&pageSize=10'
				})
				this.newsList = res.data.data
				
			},
			goDetail (newsid) {
				uni.navigateTo({
					url: '/app/getNewsDetails?newsid='+newsid 
				})
			}
		},
		onLoad(){
			this.getNews()
		}
		,
		components:{
			"pageHeader":pageHeader,
			"news-item":newsItem
		}
	}
</script>

<style lang="scss">
	.status_bar {
	      height: var(--status-bar-height);
	      width: 100%;
		  background-color: #09BB07;
	  }
</style>
